#drone {
  display: block;
  height: 200px;
  width: 200px;
  fill: white;
  width: 33%;
  transition: -webkit-transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
  margin: auto;
  fill: #da1c1c;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes bounce {
  from {
    -webkit-transform: translateY(10px) rotate(-2deg) scale(1);
            transform: translateY(10px) rotate(-2deg) scale(1);
  }
  50% {
    -webkit-transform: translateY(-10px) rotate(0deg) scale(0.95);
            transform: translateY(-10px) rotate(0deg) scale(0.95);
  }
  to {
    -webkit-transform: translateY(10px) rotate(2deg) scale(1.05);
            transform: translateY(10px) rotate(2deg) scale(1.05);
  }
}

@keyframes bounce {
  from {
    -webkit-transform: translateY(10px) rotate(-2deg) scale(1);
            transform: translateY(10px) rotate(-2deg) scale(1);
  }
  50% {
    -webkit-transform: translateY(-10px) rotate(0deg) scale(0.95);
            transform: translateY(-10px) rotate(0deg) scale(0.95);
  }
  to {
    -webkit-transform: translateY(10px) rotate(2deg) scale(1.05);
            transform: translateY(10px) rotate(2deg) scale(1.05);
  }
}

