.tickerv-wrap {
  /* background: #eee; */
  box-sizing: content-box;
  height: 130px; /* Take note of this */
  overflow: hidden; /* Hide scrollbars */
  padding: 10px;
}

/* TICKER ANIMATION */
@-webkit-keyframes tickerv {
  0% {
    margin-top: 0;
  }
  /* Quite literally -ve height of wrapper */
  20% {
    margin-top: -26px;
  } /* 1 X 25 px */
  40% {
    margin-top: -52px;
  } /* 2 X 25 px */
  60% {
    margin-top: -78px;
  } /* 3 X 25 px */
  80% {
    margin-top: -104px;
  } /* Back to first line */
  100% {
    margin-top: 0;
  } /* Back to first line */
}
@keyframes tickerv {
  0% {
    margin-top: 0;
  }
  /* Quite literally -ve height of wrapper */
  20% {
    margin-top: -26px;
  } /* 1 X 25 px */
  40% {
    margin-top: -52px;
  } /* 2 X 25 px */
  60% {
    margin-top: -78px;
  } /* 3 X 25 px */
  80% {
    margin-top: -104px;
  } /* Back to first line */
  100% {
    margin-top: 0;
  } /* Back to first line */
}
.tickerv-wrap ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
  -webkit-animation-name: tickerv;
          animation-name: tickerv; /* Loop through items */
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: cubic-bezier(1, 0, 0.5, 0);
          animation-timing-function: cubic-bezier(1, 0, 0.5, 0);
}
.tickerv-wrap ul:hover {
  /* Pause on mouse hover */
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

/* ITEMS */
.tickerv-wrap ul li {
  background-color: rgba(61, 61, 61, 0.363);
  color: rgb(199, 201, 202);
  font-size: 14px;
  line-height: 16px;
  /* padding: 18px; */
}
.tickerv-wrap ul div {
  /* background-color: rgba(238, 234, 234, 0.363); */
